<template>
<body id="poster">
  <el-form class="login-container" label-position="left" label-width="0px">
    <h3 class="login_title">用户登陆</h3>
    <el-form-item>
      <el-input type="text" v-model="loginForm.username" auto-complete="off" placeholder="Username"></el-input>
    </el-form-item>

    <el-form-item>
      <el-input
        type="password"
        v-model="loginForm.password"
        auto-complete="off"
        placeholder="Password"
      ></el-input>
    </el-form-item>

    <!-- <el-form-item style="width: 100%">
        <el-button type="primary" style="width: 100%;background: #505458;border: none" v-on:click="login">Login</el-button>
    </el-form-item>-->
    <el-button
      :type="primary"
      round
      style="width: 100%;color: #000000; background:#505458 ;border: none"
      @click="login"
    >Login</el-button>
  </el-form>
</body>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      loginForm: {
        username: "",
        password: ""
      },
      responseResult: []
    };
  },
  methods: {
    login() {
      // if (
      //   this.loginForm.username == "cdy" &&
      //   this.loginForm.password == "123"
      // ) {
      //   this.$message({
      //     message: "登录成功，正在跳转。。。。",
      //     type: "success"
      //   });
      // }
      this.$axios
        .post("http://localhost:8081/#/login", {
          username: this.loginForm.username,//cdy
          password: this.loginForm.password//1234
        })
        .then(res => {
          if (res.data === 1) {
            this.$message({
              message: "登录成功，正在跳转。。。。",
              type: "success"
            });
          }
        })
        .catch(failResponse => {});
    }
  }
};
</script>


<style>
#poster {
  background: url("../assets/8.jpg");
  background-position: center;
  height: 100%;
  width: 100%;
  background-size: cover;
  position: fixed;
}
body {
  margin: 0px;
  padding: 0;
}

.login-container {
  border-radius: 15px;
  background-clip: padding-box;
  position: relative;
  margin: 90px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}

.login_title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
</style>



